@import url(https://fonts.googleapis.com/css2?family=Libre+Baskerville&family=Roboto&display=swap);
body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{
  --base: #9494d1;
  margin: 0;
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.header{
  margin: 10px;
  font-size: 1.7rem;
  font-family: 'Libre Baskerville', serif;
}

.recipeContainer{
  display: flex;
  flex-wrap: wrap;
  width: 100%;
}

.recipeContainer>div{
  margin: 10px;
}


.selectBox{
  background: var(--base);
  padding: 5px 0;
  flex-basis: 300px;
  height: 90vh;
  display: flex;
  flex-flow: column wrap;
  justify-content: flex-start;
  align-items: center;
}

.ingredients, .directions{
  width: 450px;
}

.select{
  border-radius: 2px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: calc(100% - 4px);
  margin: 0 ;
  height: 40px;
  cursor: pointer;
  border: none;
  outline: none;
  background: hsl(240, 40%, 80%);
}
.select div{
  border-radius: 2px;
  margin: 0;
  flex-basis: 200px;
  box-sizing: border-box;
  padding: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  font-size: 1em;
}

.select button{
  color: hsl(240, 40%, 50%);
  margin: 4px;
  font-size: 12px;
}


.select .selected{
  box-sizing: border-box;
  background: #fff;
}

.toolbar{
  background-color: #fff;
  justify-content: center;
  margin-top: auto;
}

.btn{
  width: 25px;
  height: 25px;
  cursor: pointer;
  border: none;
  outline: none;
}
.btn:hover{
  background-color: rgb(199, 199, 199);
}

.btn.show{
  display: inline;
}

.btn.hide{
  display: none;
}

.head{
  font-weight: 600;
  font-size: 1.2rem;
}

.body div{
  margin:5px
}

.recipeDetail{
  flex-basis: 450px;
  flex: 2 1;
}


.dialog{
  display: none;
  align-items: center;
  justify-content: center;
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 99;
  background-color: rgba(41, 41, 41, 0.7);
}

.dialog.show{
  display: flex;
}

.modal{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 35vw;
  height: 50vh;
  background-color: hsl(240, 40%, 60%);
  border: 1px solid #fff;
}

.modal button{
  width: auto;
  margin: 5px;
}

input[type='text'] {
  margin: 5px;
  width: 30vw;
  height: 5vh;
  border: none;
  border-bottom: 1px solid #fff;
}

textarea::-webkit-input-placeholder, input::-webkit-input-placeholder{
  color: rgb(202, 202, 202);
}

textarea:-ms-input-placeholder, input:-ms-input-placeholder{
  color: rgb(202, 202, 202);
}

textarea::-ms-input-placeholder, input::-ms-input-placeholder{
  color: rgb(202, 202, 202);
}

textarea::placeholder, input::placeholder{
  color: rgb(202, 202, 202);
}

textarea, input{
  margin: 5px;
  font-family: 'Roboto', sans-serif;
  width: 30vw;
  height: 15vh;
  color: #fff;
  border: 1px solid #fff;
  outline: none;
  background-color: hsl(240, 40%, 50%);
}


@media (max-width: 800px){
  .selectBox, .recipeDetail{
    flex-basis: 100%;
    height: auto;
  }
  .select div{
    flex-basis: 80%;
  }
  .ingredients, .directions{
    width: auto;
  }
}

@media (min-width: 1400px){
  .App{
    width: 1400px;
    margin: auto;
  }
}

